const choreAdd = new Vue({
    el: '#app',
    data: {
        chore: chore,
        workers: workers,
        selected_workers: selected_workers
    },
    methods: {
        toggleReoccurring: function () {
            this.chore.reoccurring = !this.chore.reoccurring;
        }
    },
    watch: {
        'chore.reoccurring': function (val) {
            this.workers.selected_workers = [];
        }
    }
});

